import React from "react";
import store from "../store";
import { setTypingValue, sendMessage } from "../actions";
import "./MessageInput.css";

const MessageInput = ({ value }) => {
  const state = store.getState(); // up here so whenever the component re-renders, the new state value is always returned.

  const handleChange = e => {
    store.dispatch(setTypingValue(e.target.value));
  };
  const handleSubmit = e => {
    e.preventDefault();
    const { typing, activeUserId, activeChatId } = state;
    store.dispatch(sendMessage(typing, activeUserId, activeChatId));
  };

  return (
    <form className="Message" onSubmit={handleSubmit}>
      <input
        className="Message__input"
        onChange={handleChange}
        value={value}
        placeholder="write a message"
      />
    </form>
  );
};

export default MessageInput;
